"use client"

import { useState, useEffect, useCallback, useMemo } from "react"
import Image from "next/image"
import Link from "next/link"
import { Card, CardContent } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { ChevronLeft, ChevronRight } from "lucide-react"
import { SanPham, ProductFilter } from "@/api/api.type"
import { useProducts } from "@/hooks/useProducts"

// Utility functions
const formatPrice = (price: string | number | null): string => {
  if (!price) return "Li√™n h·ªá b√°o gi√°";
  const numPrice = typeof price === "string" ? parseFloat(price) : price;
  return isNaN(numPrice) ? "Li√™n h·ªá b√°o gi√°" : `${numPrice.toLocaleString("vi-VN")}ƒë`;
};

const getImageUrl = (product: SanPham): string => {
  const hasImage = product.image && product.image.length > 0;
  return hasImage
    ? `${process.env.NEXT_PUBLIC_API_MEDIA}${product.image![0].url}`
    : "/placeholder.svg";
};

export function FeaturedProducts() {
  const [currentIndex, setCurrentIndex] = useState(0)
  const [itemsPerView, setItemsPerView] = useState(5)

  // Use custom hook for products
  const filter: ProductFilter = useMemo(() => ({ 
    limit: 10, // Gi·ªõi h·∫°n s·ªë s·∫£n ph·∫©m n·ªïi b·∫≠t
    featured: true 
  }), []);
  
  const { products, loadingState, error, refetch } = useProducts(filter);

  useEffect(() => {
    const handleResize = () => {
      if (window.innerWidth < 640) {
        setItemsPerView(2)
      } else if (window.innerWidth < 768) {
        setItemsPerView(3)
      } else if (window.innerWidth < 1024) {
        setItemsPerView(4)
      } else {
        setItemsPerView(5)
      }
    }

    handleResize()
    window.addEventListener("resize", handleResize)
    return () => window.removeEventListener("resize", handleResize)
  }, [])

  // Memoized calculations
  const maxIndex = useMemo(() => Math.max(0, products.length - itemsPerView), [products.length, itemsPerView]);
  const showNavigation = useMemo(() => products.length > itemsPerView, [products.length, itemsPerView]);

  const nextSlide = useCallback(() => {
    setCurrentIndex((prev) => (prev >= maxIndex ? 0 : prev + 1))
  }, [maxIndex]);

  const prevSlide = useCallback(() => {
    setCurrentIndex((prev) => (prev <= 0 ? maxIndex : prev - 1))
  }, [maxIndex]);

  // Auto-slide functionality
  useEffect(() => {
    if (!showNavigation) return;
    
    const interval = setInterval(nextSlide, 5000); // Auto slide every 5 seconds
    return () => clearInterval(interval);
  }, [nextSlide, showNavigation]);

  // Loading skeleton component
  const LoadingSkeleton = () => (
    <section className="py-16 lg:py-24 bg-background">
      <div className="mx-auto max-w-7xl px-4 lg:px-8">
        <div className="text-center mb-12">
          <p className="text-primary text-sm tracking-[0.3em] uppercase mb-3">N·ªïi b·∫≠t</p>
          <h2 className="text-3xl lg:text-4xl font-semibold text-foreground mb-4">B·ªô S∆∞u T·∫≠p Y√™u Th√≠ch</h2>
        </div>
        <div className="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-5 gap-4 lg:gap-6">
          {[...Array(itemsPerView)].map((_, i) => (
            <Card key={i} className="border-0 shadow-none bg-card">
              <CardContent className="p-0">
                <div className="relative aspect-square overflow-hidden bg-muted animate-pulse" />
                <div className="p-4 text-center space-y-2">
                  <div className="h-4 bg-muted animate-pulse rounded" />
                  <div className="h-4 bg-muted animate-pulse rounded w-2/3 mx-auto" />
                </div>
              </CardContent>
            </Card>
          ))}
        </div>
      </div>
    </section>
  );

  // Error state
  if (loadingState === 'error') {
    return (
      <section className="py-16 lg:py-24 bg-background">
        <div className="mx-auto max-w-7xl px-4 lg:px-8 text-center">
          <p className="text-destructive mb-4">C√≥ l·ªói x·∫£y ra khi t·∫£i s·∫£n ph·∫©m: {error}</p>
          <Button onClick={fetchProducts} variant="outline">
            Th·ª≠ l·∫°i
          </Button>
        </div>
      </section>
    );
  }

  if (loadingState === 'loading') {
    return <LoadingSkeleton />;
  }

  return (
    <section className="py-16 lg:py-24 bg-background">
      <div className="mx-auto max-w-7xl px-4 lg:px-8">
        {/* Section Header */}
        <div className="text-center mb-12">
          <p className="text-primary text-sm tracking-[0.3em] uppercase mb-3">N·ªïi b·∫≠t</p>
          <h2 className="text-3xl lg:text-4xl font-semibold text-foreground mb-4">B·ªô S∆∞u T·∫≠p Y√™u Th√≠ch</h2>
          <p className="text-muted-foreground max-w-2xl mx-auto">
            Nh·ªØng m·∫´u hoa ƒë∆∞·ª£c kh√°ch h√†ng y√™u th√≠ch nh·∫•t, ƒë∆∞·ª£c c·∫Øm b·ªüi c√°c ngh·ªá nh√¢n h√†ng ƒë·∫ßu
          </p>
        </div>

        {/* Products Slider */}
        <div className="relative">
          {/* Navigation Buttons */}
          {showNavigation && (
            <>
              <Button
                variant="outline"
                size="icon"
                className="absolute left-0 top-1/2 -translate-y-1/2 -translate-x-4 z-10 rounded-full bg-white shadow-lg hover:bg-primary hover:text-white hidden md:flex"
                onClick={prevSlide}
                aria-label="S·∫£n ph·∫©m tr∆∞·ªõc"
              >
                <ChevronLeft className="h-5 w-5" />
              </Button>
              <Button
                variant="outline"
                size="icon"
                className="absolute right-0 top-1/2 -translate-y-1/2 translate-x-4 z-10 rounded-full bg-white shadow-lg hover:bg-primary hover:text-white hidden md:flex"
                onClick={nextSlide}
                aria-label="S·∫£n ph·∫©m ti·∫øp theo"
              >
                <ChevronRight className="h-5 w-5" />
              </Button>
            </>
          )}

          {/* Products Grid with Slider */}
          <div className="overflow-hidden">
            <div
              className="flex transition-transform duration-500 ease-out"
              style={{
                transform: `translateX(-${currentIndex * (100 / itemsPerView)}%)`,
              }}
            >
              {products.map((product) => (
                <ProductCard
                  key={product.documentId || product.id}
                  product={product}
                  itemsPerView={itemsPerView}
                />
              ))}
            </div>
          </div>

          {/* Mobile Navigation Dots */}
          {showNavigation && (
            <div className="flex justify-center gap-2 mt-6 md:hidden">
              {[...Array(maxIndex + 1)].map((_, index) => (
                <button
                  key={index}
                  onClick={() => setCurrentIndex(index)}
                  className={`w-2 h-2 rounded-full transition-all ${
                    index === currentIndex ? "bg-primary w-8" : "bg-gray-300"
                  }`}
                  aria-label={`ƒêi t·ªõi slide ${index + 1}`}
                />
              ))}
            </div>
          )}
        </div>

        {/* View All Button */}
        <div className="text-center mt-10">
          <Button
            variant="outline"
            size="lg"
            className="border-primary text-primary hover:bg-primary hover:text-primary-foreground bg-transparent"
            asChild
          >
            <Link href="/bo-suu-tap">Xem T·∫•t C·∫£ S·∫£n Ph·∫©m</Link>
          </Button>
        </div>
      </div>
    </section>
  )
}

// Separate ProductCard component for better reusability
interface ProductCardProps {
  product: SanPham;
  itemsPerView: number;
}

const ProductCard = ({ product, itemsPerView }: ProductCardProps) => {
  const imageUrl = getImageUrl(product);
  const price = formatPrice(product.Gia);

  return (
    <div
      className="flex-shrink-0 px-2"
      style={{ width: `${100 / itemsPerView}%` }}
    >
      <Link href={`/san-pham/${product.slug}`}>
        <Card className="group border-0 shadow-none hover:shadow-lg transition-shadow duration-300 overflow-hidden bg-card">
          <CardContent className="p-0">
            <div className="relative aspect-square overflow-hidden">
              <Image
                src={imageUrl}
                alt={product.TenHoa}
                fill
                className="object-cover group-hover:scale-105 transition-transform duration-500"
                sizes="(max-width: 640px) 50vw, (max-width: 768px) 33vw, (max-width: 1024px) 25vw, 20vw"
                priority={false}
              />
            </div>
            <div className="p-4 text-center">
              <h3 className="font-medium text-foreground text-sm lg:text-base mb-1 line-clamp-2">
                {product.TenHoa}
              </h3>
              <p className="text-primary font-semibold text-sm">{price}</p>
            </div>
          </CardContent>
        </Card>
      </Link>
    </div>
  );
};